function rgbToHex(r, g, b) {
    return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
}

export const convertSrmToRGB = (srm) => {
    
    const valueOfMap = map.find((item) => { 
        return new RegExp(item.SRM, 'i').test(srm)
    });
    if (valueOfMap) {
        const rgb = valueOfMap.RGB.split(',');
        return rgbToHex(Number(rgb[0]), Number(rgb[1]), Number(rgb[2]));
    }
    else {
        return "#fff"
    }
}
